import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Promotion } from '@app/shared/models/promotion';
import { environment } from '@env/environment';
import { map } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class PromotionService {

  private baseUrl = environment.apiUrl;
  constructor(private http: HttpClient) { }

  promotionList(searchData: String, page: Number, limt: Number, sortColumn: String, SortDirection: String) {
    console.log(this.baseUrl);
    let url: any;
    if (searchData) url = this.baseUrl + "/promotions?search=" + searchData;
    else
      url =
        this.baseUrl +
        "/promotions?page=" +
        page +
        "&limit=" +
        limt +
        "&sortColumn=" +
        sortColumn +
        "&sortDirection=" +
        SortDirection;

    return this.http.get<any>(url).pipe(map((obj) => obj || []));
  }

  createPromotion(promotion:Promotion) {
    let url = this.baseUrl + "/promotions";
    var formData: any = new FormData();
    formData.append("name", promotion.name);
    formData.append("startDate", promotion.startDate);
    formData.append("endDate", promotion.endDate);
    formData.append("bannerImage", promotion.bannerImage);
    return this.http.post(url, formData)
  }

  editPromotion(promotion:Promotion, promotionId:String) {
    let url = this.baseUrl + "/promotions/" + promotionId;
    var formData: any = new FormData();
    formData.append("name", promotion.name);
    formData.append("startDate", promotion.startDate);
    formData.append("endDate", promotion.endDate);
    formData.append("bannerImage", promotion.bannerImage);
    return this.http.put(url, formData)
  }

  getPromotionDetail(id: String) {
    let url = this.baseUrl + "/promotions/" + id;
    return this.http.get<any>(url).pipe(map((obj) => obj || {}));
  }
}
