import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { AuthenticationService } from '@app/core/authentication/authentication.service';
import { PromotionService } from '@app/core/services/promotion.service';
import { AdminSideMenu, CommonExpands } from '@app/enum/app_enum';
import { Promotion } from '@app/shared/models/promotion';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-create',
  templateUrl: './create.component.html',
  styleUrls: ['./create.component.css']
})
export class CreateComponent implements OnInit {

  public promotionForm: FormGroup
  public submitted = false;
  public routeDirLists: any = {
    routeName: [AdminSideMenu.Promotion, CommonExpands.Create],
    routeLink: ["kmax/promotion/lists"]
  }
  public promotionId: any;
  public subscription: Subscription;
  public selectedFile;

  constructor(private formBuilder: FormBuilder, private _Service: PromotionService,
    private _router: Router, private _activeRoute: ActivatedRoute, private _auth: AuthenticationService) { }

  ngOnInit(): void {
    this.promotionForm = this.formBuilder.group({
      name: ["", [Validators.required]],
      startDate: ["", [Validators.required]],
      endDate: ["", [Validators.required]],

    }
    );
    this.checkAction();
  }

  checkAction() {
    this._activeRoute.params.subscribe(params => {
      this.promotionId = params.promotionId;
      if (this.promotionId) {
        this.getUserDetail();
      }
    });
  }

  getUserDetail() {
    this.subscription = this._Service.getPromotionDetail(this.promotionId)
      .subscribe((res) => {
        let promotion = res;
        this.selectedFile=promotion.bannerImage;
        this.promotionForm = this.formBuilder.group({
          name: [promotion.name, [Validators.required]],
          startDate: [promotion.startDate, [Validators.required]],
          endDate: [promotion.endDate, [Validators.required]],
        }
        );
      })
  }

  onSubmit() {
    this.submitted = true
    if (this.promotionForm.invalid) return;
    else {
      if (this.promotionId) {
        this.onUpdate();
      } else {
        this.onCreate();
      }
    }
  }

  getFinalData() {
    let promotion = new Promotion();
    promotion.name = this.promotionForm.value.name;
    promotion.startDate = this.promotionForm.value.startDate;
    promotion.endDate = this.promotionForm.value.endDate;
    promotion.bannerImage=this.selectedFile;

    return promotion;
  }

  onCreate() {
    let finalData = this.getFinalData();
    this.subscription = this._Service.createPromotion(finalData)
      .subscribe(
        () => {
          this.goBack();
        });
  }

  onUpdate() {
    let finalData = this.getFinalData();
    finalData._id = this.promotionId;
    this.subscription = this._Service.editPromotion(finalData,this.promotionId)
      .subscribe(
        () => {
          this.goBack();
          //this.userForm.reset()
        });
  }



  get f() { return this.promotionForm.controls }


  
  goBack() {
 
      this._router.navigateByUrl('kmax/promotion/lists');

  }

  changeHandler(image){
    console.log(image.target.files);
    this.selectedFile=image.target.files[0];
  }

  ngOnDestroy() {
    if (this.subscription)
      this.subscription.unsubscribe();
  }
}

