import { Component, OnInit } from '@angular/core';
import { AuthenticationService } from '@app/core/authentication/authentication.service';
import { PromotionService } from '@app/core/services/promotion.service';
import { ActionLabel, AdminSideMenu, Label } from '@app/enum/app_enum';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-list',
  templateUrl: './list.component.html',
  styleUrls: ['./list.component.css']
})
export class ListComponent implements OnInit {
  public currentUserRole: String;
  public searchData: String;
  public activedRouteRole: String;
  public routeDirLists: any = {
    routeName: [],
    routeLink: []
  };

  public tableHeader = [Label.startDate,Label.endDate, Label.name,Label.action];
  public actionBtns = [ActionLabel.edit, ActionLabel.delete,ActionLabel.view];
  public objectLabel = ["startDate", "endDate", "name"];
  public filter: any = {};
  public actionLinks: any = {
    editLink: "",
    detailLink: "",
    deleteLink: "promotions",
    idKeyname: "promotionId",
    isDetailModal: true,
  }
  public limit: any = 10;
  public page: any = 1;
  public isAsc: boolean = true;
  public promotionLists: any = {};
  public isLoading: boolean = true;
  public totalCount: any;
  public promotionId: any;
  public subscription: Subscription;

  constructor(private _pService: PromotionService,
    private _auth: AuthenticationService,) { }

  ngOnInit(): void {
    this.currentUserRole = this._auth.currentUserValue.userType.toLowerCase();

      this.filter = {
        sortColumn: this.objectLabel[0],
        sortDirection: "asc",
        index: 0
      };
      this.promotionLists = null;
      this.isLoading = true;


      this.routeDirLists.routeName=[AdminSideMenu.Promotion];

      this.actionLinks.editLink =  "kmax/promotion/edit";
      // this.actionLinks.detailLink =  "kmax/promotion/detail";
      this.getUserLists();



  }

  getUserLists() {
    this.subscription = this._pService.promotionList(this.searchData, this.page, this.limit, this.filter.sortColumn, this.filter.sortDirection)
      .subscribe(
        data => {
          let temp: any = {};
          temp.data = data.promotions;
          temp.tableHeader = this.tableHeader;
          temp.actionBtns = this.actionBtns;
          temp.objectLabel = this.objectLabel;
          temp.btnLink = this.actionLinks;
          this.promotionLists = temp;
          this.isLoading = false;
          this.searchData = null;
          this.totalCount = data.totalCount;
        });
  }

  search(val: String) {
    this.isLoading = true;
    this.searchData = val;
    setTimeout(() => {
      this.getUserLists();
    }, 200);

  }

  pageChanges(page: any) {
    this.isLoading = true;
    this.page = page;
    setTimeout(() => {
      this.getUserLists();
    }, 200);
  }

  onFilter(e: any) {
    this.isLoading = true;
    this.filter = e;
    this.getUserLists();
  }

  ngOnDestroy() {
    this.subscription.unsubscribe();
  }

}
