import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { ListComponent } from './list/list.component';
import { RouterModule, Routes } from '@angular/router';
import { AuthGuardService } from '@app/core/guards/auth-guard.service';
import { CreateComponent } from '@app/modules/promotion/create/create.component';
import { BasicComponentsModule } from '@app/shared/basic-components/basic-components.module';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';

const routes: Routes = [
  {
    path: 'lists',
    component: ListComponent,
    canActivate: [AuthGuardService],
  },
  {
    path: 'create',
    component: CreateComponent,
    canActivate: [AuthGuardService],
  },
  {
    path: 'edit/:promotionId',
    component: CreateComponent,
    canActivate: [AuthGuardService],
  },

];


const comLists = [
  ListComponent, CreateComponent
]

@NgModule({
  declarations: [comLists],
  imports: [
    CommonModule,
    BasicComponentsModule,
    RouterModule.forChild(routes),
    ReactiveFormsModule,
    FormsModule
  ],
  exports: [
    comLists
  ]
})
export class PromotionModule { }
